@import url(https://fonts.googleapis.com/css?family=Lato:300,400&display=swap);
body {
  margin: 0;
  padding: 30px 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(data:image/png;base64,);
  background-attachment: fixed;
  background-position: center;
  background-repeat: repeat;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
  -o-user-select: none;
}

  ::-webkit-scrollbar {
    width: 8px;
  }

  ::-webkit-scrollbar-track {
    background: #ef605c;
  }

  ::-webkit-scrollbar-thumb {
    background: #fff;
  }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
  -o-user-select: none;
  padding-top: 40px;
  padding-bottom: 20px;
}

.App button {
  font-family: "Montserrat", sans-serif;
  font-size: large;
  background: #d9534f;
  color: #fff;
  border: none;
  width: 120px;
  height: 40px;
  box-shadow: 0 3px 4px rgba(0, 0, 0, 0.3);
  margin: 20px auto;
  cursor: pointer;
  outline: none;
}

.title {
  font-family: "Bigelow Rules";
  font-size: 72px;
  color: #fff;
  margin-bottom: 18px;
  letter-spacing: 10px;
  font-weight: 1000;
  text-shadow: 2px 2px 5px #c0c0c0;
}

a {
  text-decoration: none;
  color: #fff;
}

a:active {
  text-decoration: none;
  color: #fff;
}

a:link {
  text-decoration: none;
  color: #fff;
}

a:visited {
  text-decoration: none;
  color: #fff;
}

.cardList {
  display: flex;
  flex-wrap: wrap;
  align-items: space-between;
  justify-content: center;
  margin: 0 auto;
  max-width: 1200px;
}

.cardContainer {
  background-color: #eee;
  border-radius: 20px;
  box-shadow: 0 3px 15px rgba(100, 100, 100, 0.5);
  margin: 10px;
  padding: 2%;
  text-align: center;
  transition: transform 0.25s ease-out;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
  -o-user-select: none;
}

.cardContainer:hover {
  transform: scale(1.03);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.imgContainer {
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 50%;
  width: 120px;
  height: 120px;
  text-align: center;
}

.cardImage {
  margin-top: 20px;
  max-width: 90%;
}

.info {
  margin-top: 20px;
  text-transform: capitalize;
  font-family: "Lato";
}

.number {
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  font-size: 0.8em;
  padding: 5px 10px;
}

.name {
  margin: 15px 0 7px;
  letter-spacing: 1px;
  font-weight: bolder;
}

.cardContainer a {
  text-decoration: none;
  color: black;
}

.cardContainer a:link {
  text-decoration: none;
  color: black;
}

.cardContainer a:visited {
  text-decoration: none;
  color: black;
}

.cardContainer a:hover {
  text-decoration: none;
  color: #bc4242;
}
.cardContainer a:active {
  text-decoration: none;
  color: black;
}

.search {
  -webkit-appearance: none;
  border: none;
  outline: none;
  padding: 10px;
  width: 200px;
  line-height: 30px;
  margin-bottom: 30px;
  text-align: center;
}

